
























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import store from '@/store';
import tagIncomeList from '@/constants/tagIncomeList';
import tagPayList from '@/constants/tagPayList';

@Component
export default class EditLabel extends Vue {
  tags = store.state.labels;
  selected: string[] = [];
  tagList = store.state.tagList;

  created() {
    this.$store.commit('fetchTags');
  }

  get selectedTags() {
    return this.$store.state.selectedTags;
  }

  toggle(tag: string) {
    this.$store.commit('selectLabels', tag);
  }

  sure() {
    const name = document.getElementsByTagName('input')[0].value;
    const tagIncomeName = tagIncomeList.map(item => item.name);
    const tagPayName = tagPayList.map(item => item.name);
    let tagNewName1:any = [];
    let tagNewName2:any = [];
    if (this.tagList[0]) {
      tagNewName1 = this.tagList[0].map(item => {if (item.name) {return item.name;}});
    }
    if (this.tagList[1]) {
      tagNewName2 = this.tagList[1].map(item => {if (item.name) {return item.name;}});
    }
    if (name) {
      if (tagIncomeName.indexOf(name) >= 0 || tagPayName.indexOf(name) >= 0 || tagNewName1.indexOf(name) >= 0 || tagNewName2.indexOf(name) >= 0) {
        this.$message({
          message: '标签名重复',
          type: 'warning',
          duration: 1000
        });
        return;
      }
      if (name.length > 5) {
        this.$message({
          message: '最多输入五个字符哦',
          type: 'warning',
          duration: 1000
        });
        return;
      }
      if (this.selectedTags.length > 0) {
        this.$store.commit('createTags', {name: name, icon: this.selectedTags[0]});
      } else {
        this.$message({
          message: '请选择图标',
          type: 'warning',
          duration: 1000
        });
        return;
      }
    } else {
      this.$message({
        message: '请填写标签',
        type: 'warning',
        duration: 1000
      });
      return;
    }
    this.$router.replace('/labels');
  }
}
